<template>
    <div class="column">
        <form>
            <div class="col-md-8 col-md-offset-2 control">
                <label class="control-label">Write a quote</label>
            </div>
            <div class="col-md-8 col-md-offset-2 control field">
                <textarea class="textarea" rows="3" v-model="quote" @keyup.enter="addNewQuote"></textarea>
            </div>
            <div class="col-md-8 col-md-offset-2 control field">
                <button class="button is-primary is-outlined" @click.prevent="addNewQuote">Add Quote</button>
            </div>
        </form>
    </div>
    
</template>

<script>
    import {eventBus} from '../app.js';

    export default {
        data(){
            return {
                quote: ''
            };
        },
        
        methods: {
            addNewQuote(){
                let self = this;
                eventBus.$emit('newQuote', self.quote);
                self.quote = '';
            }
        }

    }
</script>


