<template>
    <div style="margin-top: 20px;">
        <h2>Quotes in your board: {{ length }} / 10 </h2>
        <div>
            <progress class="progress is-primary" max="10" :value="length" >

            </progress>
        </div>
    </div>
</template>


<script>
    export default {
        props: ['quotes', 'max'],
        data(){
            return {
                value: ''
            };
        },
        computed:{
          length() {
              return this.quotes.length;
          }
        },

    }
</script>


