<template>
    <div class="column is-4">
        <div class="card">
            <div class="card-content">
                <slot></slot>
            </div>
            <footer class="card-footer">
                <a class="card-footer-item" @click="deleting">Delete</a>
            </footer>
        </div>
    </div>
</template>

<script>
export default {
    methods:{
        deleting(){
            this.$emit('deleting');
        }
    }

}
</script>

<style>
    .panel-body {
        font-family: 'Arizonia', cursive;
        font-size: 24px;
        color: #6e6e6e;
    }

    .quote {
        cursor: pointer;
    }

    .quote:hover {
        background-color: #ffe2e2;
    }
</style>


