import Vue from 'vue';
import Router from 'vue-router';
import Quiz from '../apps/quiz/components/App.vue';
import Todo from '../apps/todo/App.vue';
import Quotes from '../apps/quotes/components/App.vue';
import News from '../apps/news/App.vue';
import Home from '../App.vue';

Vue.use(Router)

export default new Router({
  routes: [
    {path: '/', component: Home},
      {path: '/quiz', component: Quiz},
      {path: '/todo', component: Todo},
      {path: '/quotes', component: Quotes}

  ]
})



